const parallax = () => {

	window.addEventListener('scroll', function () {
		let pixelScroll = this.pageYOffset;
		let widthWindow = this.outerWidth;
		let heightContent = document.querySelector('.parallax-content').offsetHeight;
		let heightTopBlock = document.querySelector('.parallax').offsetHeight;
		let scrollContnent = pixelScroll / heightContent * 100;
		let scrollTop = pixelScroll / heightTopBlock * 100;
		let futerOpacity = 1 - 1 / 100 * scrollTop;

		let zoomFog = 1 + (widthWindow / 10000 * scrollTop);
		let zoomMontain1 = 1 + (widthWindow / 5000000 * scrollContnent);
		let zoomMontain2 = 1 + (widthWindow * 0.000005 * scrollTop);
		let zoomMontain3 = 1 + (widthWindow * 0.00001 * scrollTop);
		let biasMontain2 = widthWindow / 2000 * scrollTop;
		let biasMontain3 = widthWindow / 1500 * scrollTop;

		document.querySelector('.parallax__fog').style.transform = 'scale(' + zoomFog + ')';
		document.querySelector('.parallax__fog').style.opacity = futerOpacity;
		document.querySelector('.parallax__montain--1').style.transform = 'scale(' + zoomMontain1 + ')';
		document.querySelector('.parallax__montain--2').style.transform = 'scale(' + zoomMontain2 + ') translate3d(' + biasMontain2 + 'px, 0, 0)';
		document.querySelector('.parallax__montain--3').style.transform = 'scale(' + zoomMontain3 + ') translate3d(' + biasMontain3 + 'px, 0, 0)';
	})
}

export default parallax;