const lazyLoading = () => {
	const lazyImages = document.querySelectorAll('img[data-src], source[data-srcset]');
	const loadMapBlock = document.querySelector('.lazy-loading-map');
	const windodHeight = document.documentElement.clientHeight;
	const loadMoreBlock = document.querySelector('.lazy-loading-more');

	let lazyImagesPositions = [];
	if (lazyImages.length > 0) {
		lazyImages.forEach(img => {
			if (img.dataset.src || img.dataset.srcset) {
				lazyImagesPositions.push(img.getBoundingClientRect().top + pageYOffset);
				lazyScrollCheck();
			}
		});
	}

	window.addEventListener("scroll", lazyScroll);

	function lazyScroll() {
		if (document.querySelectorAll('img[data-src], source[data-srcset]').length > 0) {
			lazyScrollCheck();
		}
		if (!loadMapBlock.classList.contains('lazy-loading-load')) {
			getMap();
		}
		if (!loadMoreBlock.classList.contains('lazy-loading-loading')) {
			loadMore();
		}
	}

	function lazyScrollCheck() {
		let imgIndex = lazyImagesPositions.findIndex(
			item => pageYOffset > item - windodHeight
		);
		if (imgIndex >= 0) {
			if (lazyImages[imgIndex].dataset.src) {
				lazyImages[imgIndex].src = lazyImages[imgIndex].dataset.src;
				lazyImages[imgIndex].removeAttribute('data-src');
			} else if (lazyImages[imgIndex].dataset.srcset) {
				lazyImages[imgIndex].srcset = lazyImages[imgIndex].dataset.srcset;
				lazyImages[imgIndex].removeAttribute('data-srcset');
			}
			delete lazyImagesPositions[imgIndex];
		}
	}

	function getMap() {
		const loadMapBlockPosition = loadMapBlock.getBoundingClientRect().top + pageYOffset;
		if (pageYOffset > loadMapBlockPosition - windodHeight) {
			const loadMapUrl = loadMapBlock.dataset.map;
			if (loadMapUrl) {
				loadMapBlock.insertAdjacentHTML(
					"beforeend",
					`<iframe src="${loadMapUrl}" width="600" height="450" style="border:0;" allowfullscreen=""></iframe>`
				);
				loadMapBlock.classList.add('lazy-loading-load');
			}
		}
	}

	function loadMore() {
		const loadMoreBlockPosition = loadMoreBlock.getBoundingClientRect().top + pageYOffset;
		const loadMoreBlockHeight = loadMoreBlock.offsetHeight;

		if (pageYOffset > (loadMoreBlockPosition + loadMoreBlockHeight) - windodHeight) {
			getConten();
		}
	}

	async function getConten() {
		if (!document.querySelector('.lazy-loading-temp')) {
			loadMoreBlock.insertAdjacentHTML(
				'beforeend',
				`<div class="lazy-loading-temp"></div>`
			)
		}

		loadMoreBlock.classList.add('lazy-loading-loading');

		let response = await fetch('_more.html', {
			method: 'GET',
		});

		if (response.ok) {
			let result = await response.text();
			loadMoreBlock.insertAdjacentHTML('beforeend', result);
			loadMoreBlock.classList.remove('lazy-loading-loading');

			if (document.querySelector('.lazy-loading-temp')) {
				document.querySelector('.lazy-loading-temp').remove();
			}
		} else {
			alert("Ошибка");
		}
	}
}

export default lazyLoading;