import city from "./city/city.js";
import hamburger from "./hamburger/hamburger.js";
import spoler from "./spoler/spoler.js";
import parallax from "./parallax/parallax.js";
import lazyLoading from "./lazy-loading/lazy-loading.js";
import works from "./js-works/works.js";
import toTop from "./to-top/to-top.js"
import linkAnchor from "./link-anchor/link-anchor.js";
import menu from "./menu-css/menu.js";

window.addEventListener("DOMContentLoaded", () => {
	if ($('.parallax').length) {
		parallax();
	}
	if ($('.spoler').length) {
		spoler();
	}
	hamburger();
	city();
	works();
	linkAnchor();
	toTop();
	if ($('.menu').length) {
		menu();
	}
	if ($('.lazy-loading').length) {
		lazyLoading();
	}
});